<template>
	<nav class="component_w_ts">
		<div class="c_w_ts_year" @click="showYear">
			{{y + "年"}}
		</div>
		<ul class="c_w_ts_year_s" v-show="flag">
			<li v-for="(item,index) in _year" @click="changeYear(index)">{{item + "年"}}</li>
		</ul>
		<div class="c_w_ts_month">
			<ul :style="getUlWidth">
				<li v-for="(item,index) in _month" :class="mIndex === index?'active':''" @click="selectMonth(index)">{{item + "月"}}</li>
			</ul>
		</div>
	</nav>
</template>

<script>
	export default {
		name:'time-select',
		props:{
			year:{
				type:Array,
				defalut:[]
			},
			month:{
				type:Array,
				default:[]
			}
		},
		data(){
			return {
				flag:false,
				y:'',
				mIndex:0
			}
		},
		mounted(){
		},
		computed:{
			_year(){
				this.y = this.year && this.year[0];
				return this.year;
			},
			_month(){
				return this.month;
			},
			getUlWidth(){
				let width = this.month.length * 75;
				return "width:" + width + "px";
			}
		},
		methods:{
			showYear(){
				this.flag = !this.flag;
			},
			changeYear(val){
				this.y = this.year[val];
				this.flag = !this.flag;
				this.dispatchEvent();
			},
			selectMonth(val){
				this.mIndex = val;
				this.dispatchEvent();
			},
			dispatchEvent(){
				this.$emit('changeTime',{y:this.y,m:this.month[this.mIndex]})
			}
		},
		components:{
		}
	}
</script>

<style scoped>
	.component_w_ts{
		position:relative;
		width:100%;
		height:30px;
		line-height:30px;
		color:#717989;
		background:#243044;
		text-align:center;
	}
	.c_w_ts_year{
		width:20%;
		height:100%;
		float:left;
		color:#51d1b1;
	}
	.c_w_ts_year_s{
		position:absolute;
		top:30px;
		left:0;
		width:20%;
		background:#243044;
	}
	.c_w_ts_month{
		float:left;
		width:80%;
		overflow:scroll;
	}
	.c_w_ts_month ul{
		overflow:hidden;
	}
	.c_w_ts_month li{
		width:74px;
		float:left;
	}
	.c_w_ts_month li.active{
		color:#51d1b1;
	}
</style>


