<template>
    <nav class="index_nav">
    	<router-link class="no_a_defult" :class="active === 0? 'active' : ''" to="/"><span @click="clickHandler(0)"><i class="iconfont iconfont_se">&#xe636;</i>赛程</span></router-link>
		<router-link class="no_a_defult" :class="active === 1? 'active' : ''" to="/my" ><span @click="clickHandler(1)"><i class="iconfont iconfont_my">&#xe65b;</i>我的</span></router-link>
    </nav>
</template>
<script>
  export default {
  	name:'nav',
  	props:{
  		active:{
  			type:Number,
  			default:0
  		}
  	},
    data(){
      return {
      	index:0
      }
    },
    methods:{
    	clickHandler(val){
    		this.index = val;
    	}
    }
  }
</script>

<style scoped lang="scss">
	@import "../assets/sass/function.scss";
	.no_a_defult{
		text-decoration:none;
		color:black;
	}
	.index_nav{
		position:fixed;
		bottom:0;
		left:0;
		width:100%;
		height:38px;
		line-height:38px;
		display:flex;
		text-align:center;
		background:#161923;
	}
	.index_nav a{
		flex:1;
		font-size:16px;
		font-weight:100;
		color:#748cae;
		span{
			display:block;
			width:100%;
			height:100%;
		}
		i{
			font-weight:100;
			font-style:normal;
			display:inline-block;
			height:100%;
			margin-top:-3px;
			vertical-align:middle;
			margin-right:8px;
			vertical-align:middle;
		}
		i.iconfont_my{
			font-size:26px;
		}
		i.iconfont_se{
			font-size:24px;
		}
	}
	.index_nav a.active{
		color:#51d1b1;
	}
</style>


