<template>
	<section>
		<Time-select :month="month" :year="year" @changeTime="getTime"></Time-select>
		<article>
			<h2>终极快速筹码大型锦标资格赛</h2>
			<p class="sc_des_tips">尚未开始</p>
			<div class="container">
				<img src="./../assets/images/test_001.png" alt="">	
				<p class="fontl_pad8">
					<span>时间：</span>
					<time>2017.07.11 - 2017.-7.21</time>
				</p>
				<p class="fontl_pad8">
					<span>奖池：</span>
					<span>888万</span>
				</p>
				<address class="fontl_pad8">
					<span>地址：</span>
					<p class="sc_des_fl">
						河北省廊坊市三河市燕郊镇行宫大街与燕顺路交汇处圣得花园46A
					</p>
				</address>
				<p class="fontl_pad8">
					<span>简介：</span>
					<span class="sc_des_fl">
						Lorem ipsum dolor sit amet, consectetur adipiscing
			            elit. Aenean euismod bibendum laoreet. Poroin
			            elit. Aenean euismod bibendum laoreet. Poroin
					</span>
				</p>
				<button class="show_schedule_btn" type="button" @click="goLookScheduleHandler">查看赛程</button>
			</div>
		</article>
		<ul class="page_btn_dot c">
			<li class="active"></li>
			<li></li>
			<li></li>
		</ul>
		<Nav-bar :active="0"></Nav-bar>
	</section>
</template>

<script>
	import TimeSelect from "@/components/timeselect";
	import NavBar from '@/page/nav.vue'
	export default {
		data(){
			return {
				month:[1,2,3,4,5],
				year:[2017,2018]
			}
		},
		components:{
			TimeSelect,NavBar
		},
		methods:{
			getTime(val){
				console.log(val);
			},
			goLookScheduleHandler(){
				this.$router.push('LookSchedule')
			}
		}
	}
</script>

<style scoped lang="scss">
	@import "./../assets/sass/function.scss";
	section{
		text-align:center;
	}
	article{
		position:relative;
		width:px2em(582px);
		min-height:px2em(970px);
		margin:px2em(26px) auto 0;
	}
	article h2{
		font-size:px2em(28px);
		color:#3ea88d;
	}
	article .sc_des_tips{
		color:#717989;
		padding:8px 0;
	}
	article .container{
		position:relative;
		min-height:px2em(870px);
		overflow:hidden;
		border-radius:5px;
		background:#ffffff;
		box-shadow:0 0 10px 4px rgba(0,0,0,.6);
		img{
			width:100%;
		}
		.sc_des_fl{
			display:inline-block;
			vertical-align:top;
			max-width:80%;
		}
		.fontl_pad8{
			padding:0 8px;
			text-align:left;
			line-height:2;
		}
	}
	.show_schedule_btn{
		position:absolute;
		bottom:0px;
		left:50%;
		transform:translateX(-50%);
		padding:5px 10px;
		border:none;
		outline:none;
		background:transparent;
		color:#3ea88d;
		font-size:px2em(28px)
	}
	.page_btn_dot{
		display:inline-block;
		margin:20px auto;
	}
	.page_btn_dot li{
		float:left;
		width:5px;
		height:5px;
		border-radius:50%;
		background:rgba(255,255,255,0.3);
		margin-left:20px;
	}
	.page_btn_dot li.active{
		background:rgba(255,255,255,0.8);
	}
	.page_btn_dot li:nth-of-type(1){
		margin-left:0;
	}
</style>


